import React from 'react';
import { Link } from 'react-static';

import './site-map-default.sass';

const displaySlug = (slug) => {
	const arr = slug.split('/');
	const word = arr[arr.length - 1];

	return word.slice(0, 1).toUpperCase() + word.slice(1);
};

function SiteMap({ pages }) {
	return (
		<nav className="site-map">
			{pages.map((slug, index) => {
				return (
					<React.Fragment key={`${slug}-footer-${index}`}>
						<Link className="site-map__link" to={slug}>
							<span className="site-map__label">{displaySlug(slug)}</span>
						</Link>
						{index < pages.length - 1 ? (
							<span className="site-map__divider">|</span>
						) : null}
					</React.Fragment>
				);
			})}
		</nav>
	);
}

export default SiteMap;



