import React from 'react';
import { connect } from 'react-redux';
import withSize from 'react-size-components';

import Nav from './Nav';
import Title from './Title';
import MobileNav from './MobileNav';

import { menuOn, menuOff } from './menu-actions';

import './header-default.sass';

// const title = process.env.REACT_APP_SITE_TITLE;

const MobileHeaderMarkup = ({
	isMenuOn,
	menuOff,
	menuOn,
	pages,
	siteMetadata,
} = {}) => {
	const icon = isMenuOn ? 'times' : 'bars';
	const iconClass = `fa fa-${icon} fa-lg mobile-header__hamburger`;

	const style = {}; //isMenuOn ? { borderBottom: 'white 1px solid' } : null;
	return (
		<React.Fragment>
			<div className="mobile-header__container" style={style}>
				<header className="mobile-header">
					<button className={iconClass} onClick={isMenuOn ? menuOff : menuOn} />
					<Title text={siteMetadata.title} />
				</header>
			</div>
			<MobileNav pages={siteMetadata.navLayout.mainNav} />
		</React.Fragment>
	);
};

const HeaderMarkup = ({
	isMobile,
	isMenuOn,
	menuOn,
	menuOff,
	siteMetadata,
}) => {
	return (
		<div className="header__container">
			<header className="header">
				<Title text={siteMetadata.title} />
				<Nav pages={siteMetadata.navLayout.mainNav} />
			</header>
		</div>
	);
};

const mapStateToProps = (state) => ({
	isMenuOn: state.menu.isMenuOn,
});

const mapDispatchToProps = (dispatch) => ({
	menuOn: () => dispatch(menuOn()),
	menuOff: () => dispatch(menuOff()),
});

const MobileHeader = connect(
	mapStateToProps,
	mapDispatchToProps
)(MobileHeaderMarkup);
const Header = connect(
	mapStateToProps,
	mapDispatchToProps
)(HeaderMarkup);

const HeaderContainer = (props) => {
	return !props.sizes.mobile ? (
		<Header {...props} />
	) : (
		<MobileHeader {...props} />
	);
};

export default connect(mapStateToProps)(
	withSize({ mobile: true })(HeaderContainer)
);



