import React from 'react';
import { Link } from 'react-static';

import './nav-default.sass';

const displaySlug = (slug) => {
	const arr = slug.split('/');
	const word = arr[arr.length - 1];

	return word.slice(0, 1).toUpperCase() + word.slice(1);
};

function Nav(props) {
	const Links = props.pages.map((slug, index, arr) => {
		const modifier = index === 0 ? 'menu__item-first' : 'menu__item-nth';

		return (
			<React.Fragment key={slug}>
				<Link className={'menu__item ' + modifier} to={slug}>
					<span>{displaySlug(slug)}</span>
				</Link>
				{index < arr.length - 1 ? <span>|</span> : null}
			</React.Fragment>
		);
	});

	return <nav className="menu">{Links}</nav>;
}

export default Nav;



