import React from 'react';
import ReactMarkdown from 'react-markdown';
import Slides from './Slides';
import qs from 'qs';

import './project.sass';

const createURL = (url, params) => {
	// console.log(url + qs.stringify(params, { arrayFormat: 'brackets' }));
	return url + '/?' + qs.stringify(params, { arrayFormat: 'brackets' });
};

const createSources = (src) => {
	const widths = [400, 600, 1000, 1200];
	const sources = widths.map((width) => {
		return (
			createURL('http://localhost:3000', {
				uri: decodeURIComponent(src),
				resize: {
					w: width,
				},
				format: 'webp',
			}) +
			' ' +
			`${width}w`
		);
	});

	return {
		srcSet: sources.reduce((a, b) => `${a},${b}`),
	};
};

// console.log(createSrcSet('https://image.com'));

const Project = ({ project }) => {
	return (
		<article className="project">
			<img
				className="project__image"
				// src={project.acf.main_image.url}
				sizes="75vw"
				{...createSources(project.acf.main_image.url)}
				alt={`project-main-${project.slug}`}
			/>
			<h1 className="project__h1">{project.title.rendered}</h1>
			<ReactMarkdown
				className="project__description"
				source={project.acf.description}
			/>
			{project.acf.collaborators ? (
				<p>Collaboration: {project.acf.collaborators}</p>
			) : null}
			<section className="project__slides">
				{project.acf.slides
					? project.acf.slides.map((slide, index) => {
							return (
								<div className="slide" key={`slide-${index}`}>
									<Slides slide={slide} />
								</div>
							);
					  })
					: null}
			</section>
		</article>
	);
};

export default Project;



