import React from 'react';

const Gallery = ({ slide } = {}) => {
	const classes = `slide__gallery slide__gallery-${slide.display}`;

	const images = slide.images.map((image) => {
		return (
			<img
				className="slide__galleryimage"
				key={image.id}
				src={image.url}
				alt={image.name}
			/>
		);
	});

	return <article className={classes}>{images}</article>;
};

export default Gallery;



