import React from 'react';
import ReactMarkdown from 'react-markdown';
import YouTube from './YouTube';
import SelfHostedVideo from './SelfHostedVideo';
import Gallery from './Gallery';

const SlideSwitch = ({ slide } = {}) => {
	const type = slide['acf_fc_layout'];
	switch (type) {
		case 'image':
			return (
				<img
					className="slide__image"
					src={slide.images.url}
					alt={slide.images.name}
				/>
			);
		case 'text':
			return <ReactMarkdown className="slide__text" source={slide.text} />;
		case 'heading':
			return <h2 className="slide__h2">{slide.heading}</h2>;
		case 'youtube':
			return <YouTube slide={slide} />;
		case 'video':
			return <SelfHostedVideo slide={slide} />;
		case 'gallery':
			return <Gallery slide={slide} />;
		default:
			return null;
	}
};

export default SlideSwitch;



