import React, { Component } from 'react';
import { Link } from 'react-static';

import './thumbnails.sass';

const scrollTop = () => window.scroll(0, 0);

class Thumbnails extends Component {
	render() {
		const list = this.props.projects.map((project) => {
			const to = `/projects/${project.slug}`;
			const src = project.acf.thumbnail.sizes.medium_large;
			return (
				<article className="portfolioitem" key={project.id}>
					<Link className="portfolioitem__link" to={to} onClick={scrollTop}>
						<div className="portfolioitem__crop">
							<img
								className="portfolioitem__thumbnail"
								src={src}
								alt={project.slug}
							/>
						</div>
						<h1 className="portfolioitem__h1">{project.title.rendered}</h1>
					</Link>
				</article>
			);
		});

		return <section className="portfolio">{list}</section>;
	}
}

export default Thumbnails;



