import React from 'react';
import SiteMap from './SiteMap';
import withSize from 'react-size-components';

import './footer-default.sass';

const Footer = ({ siteMetadata } = {}) => {
	const year = new Date().getFullYear();
	return (
		<footer className="footer">
			<SiteMap pages={siteMetadata.navLayout.footerNav} />
			<p className="footer__text">
				<i className="fa fa-code footer__icon" /> Web Design by{' '}
				<a
					className="footer__link"
					href="https://www.linkedin.com/in/nickmyersnyc/"
					rel="noopener noreferrer"
				>
					Nick Myers
				</a>
			</p>
			<p className="footer__text">
				<i className="fa fa-copyright footer__icon" /> {year}{' '}
				{siteMetadata.title}
			</p>
		</footer>
	);
};

export default withSize({ mobile: true })(Footer);



