import React from 'react';
import { Link } from 'react-static';
import { connect } from 'react-redux';

import { menuOff } from '../menu-actions';

import './mobile-nav-default.sass';

const displaySlug = (slug) => {
	const arr = slug.split('/');
	const word = arr[arr.length - 1];

	return word.slice(0, 1).toUpperCase() + word.slice(1);
};

const MobileNav = ({ pages, mobileMenuStyle, menuOff } = {}) => {
	return (
		<nav style={mobileMenuStyle} className="mobile-menu">
			{pages.map((slug, index) => {
				return (
					<Link
						key={`${slug}-mobile-nav-${index}`}
						className="mobile-menu__item"
						to={slug}
						onClick={menuOff}
					>
						<span>{displaySlug(slug)}</span>
					</Link>
				);
			})}
		</nav>
	);
};

const mapStateToProps = (state) => ({
	mobileMenuStyle: state.menu.mobileMenuStyle,
});

const mapDispatchToProps = (dispatch) => ({
	menuOff: () => {
		dispatch(menuOff());
	},
});

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(MobileNav);



